"use client";
import Sidebar from "../../components/sidebar";

export default function AdminLayout({ children }) {
  return (
    <div className="flex h-screen overflow-hidden">
      <Sidebar />
      <main className="ml-64 w-full h-screen overflow-y-auto bg-gray-100 p-6">
        {children}
      </main>
    </div>
  );
}
